import _ from 'lodash';
import { withRouter } from 'react-router-dom';
import React, { Component, Fragment } from 'react';
import { Col, Row } from 'reactstrap';
import PropTypes from 'prop-types';
import InfoPage from 'components/Templates/InfoPage/InfoPage';
import Mouse from 'components/Rewards/Mouse';
import LoadingScreenV2 from 'components/Templates/LoadingScreenV2/LoadingScreenV2';

import Main from 'assets/images/Moovaz/Main.svg';
import MoovazImg from 'assets/images/Moovaz/Moovaz.png';
import Sub from 'assets/images/Moovaz/Sub.svg';
import Travel from 'assets/images/Moovaz/Travel.svg';
import TermLife from 'assets/images/Moovaz/TermLife.svg';
import PA from 'assets/images/Moovaz/PA.svg';
import Motor from 'assets/images/Moovaz/Motor.svg';
import Pet from 'assets/images/Moovaz/Pet.svg';
// import Hospitalization from 'assets/images/Moovaz/Hospitalization.svg';

import Enquiry from './Enquiry/Enquiry';

import './Moovaz.scss';


const data = [{
  img: Travel,
  desc: 'Travel',
  url: '/travel',
}, {
  img: TermLife,
  desc: 'Term\nLife',
  url: '/life',
},
{
  img: PA,
  desc: 'Personal\nAccident',
  url: '/personal-accident',
},
 {
  img: Motor,
  desc: 'Motor',
  url: '/motor',
}, {
  img: Pet,
  desc: 'Pet',
  url: '/pet',
}];

class Moovaz extends Component {
  constructor(props) {
    super(props);

    this.state = {
      showLoading: false,
      showLoadingError: false,
    };

    this.handleAPI = this.handleAPI.bind(this);
    this.showLoading = this.showLoading.bind(this);
    this.showLoadingError = this.showLoadingError.bind(this);
  }

  componentDidMount() {
    window.scrollTo(0, 0);
  }

  async showLoading(show) {
    await new Promise((resolve) => { this.setState({ showLoading: show }, resolve); });
  }

  async showLoadingError(show) {
    await new Promise((resolve) => { this.setState({ showLoadingError: show }, resolve); });
  }

  async handleAPI(apiCallback, overwriteWarning = false) {
    const { isFromSession } = this.state;

    try {
      clearInterval(this.showLoadigInterval);
      if (!isFromSession) {
        this.showLoadigInterval = setInterval(() => this.setState({ showLoading: true }), 300);
      }

      const result = await apiCallback();

      clearInterval(this.showLoadigInterval);
      this.setState({
        showLoadingError: overwriteWarning ? false : !result,
        showLoading: overwriteWarning ? false : !result,
      });
      return result;
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }

  render() {
    const { showLoading, showLoadingError } = this.state;
    const { history } = this.props;

    return (
      <Fragment>
        <InfoPage
          className="moovaz-container info-page-margin"
          noPolicyPal
          subscribeOverwrite={<Fragment />}
          noAutoMargin
          mode="full"
        >
          <Col xs="12">
            <div className="why-partner">
              <Row className="align-items-center" noGutters>
                <Col xs={{ size: 12 }} lg={{ size: 7 }}>
                  <h1 className="title">Expats</h1>
                  <p className="desc">Ensuring you and your family gets the protection they deserve.</p>
                  <div className="main-img">
                    <img src={Main} alt="Main.svg" />
                  </div>
                  <div className="moovaz-logo">
                    <span className="desc">Partnership with</span>
                    <img src={MoovazImg} alt="Moovaz.png" />
                  </div>
                </Col>

                <Col xs={{ size: 12 }} lg={{ size: 4, offset: 1 }}>
                  <Enquiry
                    handleAPI={this.handleAPI}
                  />
                </Col>
              </Row>
            </div>
          </Col>

          <Col xs="12 mt-4">
            <Mouse className="d-none d-md-block ml-auto mr-auto mt-2 mb-5" />
          </Col>

          <Col xs="12">
            <Row className="margin-padding dashboard-container form-landing-information-container" noGutters>
              <Col className="header-container" xs={12}>
                <h2 className="main-title">WHY</h2>
                <h2 className="sub-title center">Why enquire from us?</h2>
              </Col>

              <Col xs={{ size: 12 }} md={{ size: 4, offset: 2 }}>
                <div className="sub-img-container">
                  <img src={Sub} alt="sub.svg" />
                </div>
              </Col>

              <Col xs={{ size: 12 }} md={{ size: 4, offset: 1 }}>
                <h2 className="title">It’s Convenient!</h2>
                <p className="desc">Moving and working in a foreign country is troublesome. Let us assist you and your family’s safety in a few simple steps.</p>
                <p className="blue">With just one form, you can cover your family on the following insurances</p>

                <Row noGutters>
                  {
                    _.map(data, (d, index) => (
                      <Col className="icon-container" key={`icon-${index}`} xs={6} sm={4}>
                        <button
                          type="button"
                          onClick={() => history.push(d.url)}
                        >
                          <img src={d.img} alt={`${d.desc}.png`} />
                          <p className="icon-text">{d.desc}</p>
                        </button>
                      </Col>
                    ))
                  }
                </Row>
              </Col>
            </Row>
          </Col>

        </InfoPage>
        <LoadingScreenV2
          isLoading={showLoading}
          isLoadingError={showLoadingError}
          message={<h5>Please try again.</h5>}
          showLoading={this.showLoading}
          showLoadingError={this.showLoadingError}
          noLogos
        />
      </Fragment>
    );
  }
}

Moovaz.defaultProps = {
};

Moovaz.propTypes = {
  history: PropTypes.object.isRequired,
};

export default withRouter(Moovaz);
