import _ from 'lodash';
import React, { Component } from 'react';
import { Col, Row } from 'reactstrap';
import PropTypes from 'prop-types';

import FieldGenerator from 'components/FormsV3/FieldGenerator';
import { validateFormValue, validateForm } from 'lib/validatorsV2';
import Button from 'components/FormsV3/Button/Button';
import CheckoutComplete from 'components/Templates/CheckoutPopup/CheckoutComplete';

import { sendEmailForm } from 'actions/insuranceActions';

// import './Enquiry.scss';

const lookingData = [{
  size: 4,
  title: 'Myself',
  slug: 'myself',
  type: 'checkbox',
}, {
  size: 4,
  title: 'My Spouse',
  slug: 'my_spouse',
  type: 'checkbox',
}, {
  size: 4,
  title: 'My Children',
  slug: 'my_children',
  type: 'checkbox',
}];

const interestsData = [{
  size: 6,
  title: 'Death/Critical Illness',
  slug: 'death_critical_illness',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Travel',
  slug: 'travel',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Hospitalisation/Medical',
  slug: 'hospitalisation_medical',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Motor',
  slug: 'motor',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Personal Accident',
  slug: 'pa',
  type: 'checkbox',
}, {
  size: 6,
  title: 'Tenent',
  slug: 'tenent',
  type: 'checkbox',
}];

const forms = [
  {
    size: 12,
    title: 'Email',
    slug: 'email',
    type: 'email',
    is_required: true,
  }, {
    size: 6,
    title: 'Full name',
    slug: 'name',
    type: 'text',
    is_required: true,
  }, {
    size: 6,
    title: 'Mobile number',
    slug: 'contact',
    type: 'number',
    is_required: true,
  }, {
    size: 12,
    title: 'Drop your enquiry here',
    slug: 'note',
    type: 'textarea',
    is_required: true,
  },
];

const allForms = [...interestsData, ...forms];

class Enquiry extends Component {
  constructor(props) {
    super(props);

    this.state = {
      values: {},
      errors: {},
    };

    this.onChangeValue = this.onChangeValue.bind(this);
    this.handleSubmit = this.handleSubmit.bind(this);
  }

  // Update form values on change
  async onChangeValue(type, name, value) {
    const { values, errors } = this.state;

    const error = errors && errors[name];
    let newError = {};

    if (error) {
      newError = { [name]: await validateFormValue(name, { type, value }, values, allForms) };
    }

    this.setState({
      values: {
        ...values,
        [name]: { type, value },
      },
      errors: { ...errors, ...newError },
    });
  }

  async handleSubmit() {
    const { values, errors } = this.state;
    const { handleAPI } = this.props;

    try {
      const fieldsToCheck = [
        'name',
        'contact',
        'email',
        'note',
      ];

      const newErrors = await validateForm(values, fieldsToCheck, forms);
      if (!_.isEmpty(newErrors)) {
        this.setState({
          errors: newErrors,
        });

        return;
      }

      const requestData = {
        form_category: 'Moovaz Enquiry',
        input_data: _.map(forms, f => ({
          name: f.title,
          value: values[f.slug].value,
          type: 'text',
        })),
      };

      const interests = _.reduce(interestsData, (result, interest) => {
        try {
          if (values[interest.slug].value) {
            result.push(interest.title);
          }
        } catch (error) {
          // eslint-disable-next-line
        }

        return result;
      }, []);

      requestData.input_data.push({
        name: 'Interests',
        value: _.isEmpty(interests) ? ['N/A'] : interests,
        type: 'array',
      });

      const looking = _.reduce(lookingData, (result, look) => {
        try {
          if (values[look.slug].value) {
            result.push(look.title);
          }
        } catch (error) {
          // eslint-disable-next-line
        }

        return result;
      }, []);

      requestData.input_data.push({
        name: 'Looking to insure',
        value: _.isEmpty(looking) ? ['N/A'] : looking,
        type: 'array',
      });

      const result = await handleAPI(() => sendEmailForm(requestData));

      if (result) {
        if (this.checkoutComplete) {
          setTimeout(() => {
            this.checkoutComplete.openCheckoutComplete();
          }, 300);
        }
      } else {
        this.setState({
          errors: {
            ...errors,
            note: 'Request failed! Please try again.',
          },
        });
      }
    } catch (error) {
      // eslint-disable-next-line
    }
  }

  render() {
    const { values, errors } = this.state;

    try {
      return (
        <div className="landing-form-container-new landing-form-container-border">
          <Row noGutters>
            <Col xs={12}>
              <div className="input-wrapper-main-v3">
                <p className="input-wrapper-desc">Who are you looking to insure?</p>
              </div>
            </Col>
            {
              _.map(lookingData, (form, index) => (
                <FieldGenerator
                  key={`business-form-${index}`}
                  values={values}
                  errors={errors}
                  fieldData={form}
                  onChangeValue={this.onChangeValue}
                  extraInfo={form.extra_info && <p>{form.extra_info}</p>}
                />
              ))
            }
            <Col xs={12}>
              <div className="input-wrapper-main-v3">
                <p className="input-wrapper-desc">Choose your area of concern (select all that apply)</p>
              </div>
            </Col>
            {
              _.map(interestsData, (form, index) => (
                <FieldGenerator
                  key={`business-form-${index}`}
                  values={values}
                  errors={errors}
                  fieldData={form}
                  onChangeValue={this.onChangeValue}
                  extraInfo={form.extra_info && <p>{form.extra_info}</p>}
                />
              ))
            }
            <Col xs={12}>
              <div className="input-wrapper-main-v3">
                <p className="input-wrapper-desc">Details</p>
              </div>
            </Col>
            {
              _.map(forms, (form, index) => (
                <FieldGenerator
                  key={`business-form-${index}`}
                  values={values}
                  errors={errors}
                  fieldData={form}
                  onChangeValue={this.onChangeValue}
                  extraInfo={form.extra_info && <p>{form.extra_info}</p>}
                />
              ))
            }
            <Col className="d-flex justify-content-center" xs={12}>
              <Button
                variant="medium-flat-green"
                height={48}
                width={162}
                onClick={this.handleSubmit}
              >
                <p>Submit</p>
              </Button>
            </Col>
          </Row>
          <CheckoutComplete
            className="blue-popup-backdrop"
            isQuoteOnly
            upperText="Submission Successful!"
            midText="Thank You for Choosing PolicyPal! We will be contacting you within 48 hours."
            buttonText="Confirm"
            wrappedComponentRef={node => this.checkoutComplete = node}
            callback={() => {
              this.setState({
                values: {},
                errors: {},
              });
            }}
          />
        </div>
      );
    } catch (error) {
      // eslint-disable-next-line
    }

    return null;
  }
}

Enquiry.defaultProps = {
};

Enquiry.propTypes = {
  handleAPI: PropTypes.func.isRequired,
};

export default Enquiry;
